.reserch-news {
    padding-top: 0;

    background-color: $color__red;
    .btn-underline {
        &:hover,
        &:focus {
            color: $color__dark;

            &:after {
                border-bottom: 1px solid $color__dark;
            }
        }
    }
    .background {
        z-index: 0;

        width: 100%;

        transition: .25s linear;
    }
    @media (min-width: 992px) {
        background-color: transparent;
    }
    .container {
        @media (min-width: 1200px) {
            max-width: 1140px;
        }
    }
    &_forms {
        padding: 50px;

        border-radius: $border__radius;
        background-color: $color__grey;
        box-shadow: $box__shadow;
        @media (max-width: 420px) {
            padding: 25px;
        }
    }
    &_cards {
        position: relative;

        display: block;
        overflow: hidden;

        min-height: 160px;
        margin-top: 15px;
        padding: 40px;

        text-align: right;

        color: $color__white;
        border-radius: $border__radius;
        box-shadow: $box__shadow;
        .background:after {
            position: absolute;
            top: 0;
            left: 0;

            display: block;

            width: 100%;
            height: 100%;

            content: '';
            transition: .25s linear;

            background: rgba(0, 0, 0, .45);
        }
        @media (max-width: 420px) {
            min-height: inherit;
            padding: 10px 25px 0;
        }
        .svg-icon {
            position: relative;
            top: -2px;

            display: inline-block;

            width: 18px;
            height: 10px;

            transform: rotate(-90deg);
            ;
        }
        p {
            font-size: 1.2em;

            position: relative;
            z-index: 2;
        }
        &:hover,
        &:focus {
            color: $color__white;
            .background:after {
                transition: .25s linear;

                background: rgba(0, 0, 0, .75);
            }
        }
    }
}